#chatbot-container {
    position: fixed;
    bottom: 20px; /* Adjust the position as needed */
    right: 20px; /* Adjust the position as needed */
    z-index: 9999; /* Ensure the chat bot appears above other elements */
  }
  
  #chatbot-button {
    background-color: #ffa046;
    color: white;
    border: none;
    padding: 2px 4px;
    font-size: 35px;

    cursor: pointer;
  }
  
  #chatbot-window {
    display: none; /* Hide the chat bot window by default */
    background-color: #f1f1f1;
    position: absolute;
    bottom: 60px; /* Adjust the position as needed */
    right: 0;
    width: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  #chatbot-window.show {
    display: block; /* Show the chat bot window when necessary */
  }
  
  #chatbot-messages {
    height: 200px;
    overflow-y: scroll;
    padding: 10px;
  }
  
  #chatbot-input {
    width: 100%;
    padding: 10px;
    border: none;
    border-top: 1px solid #ccc;
    box-sizing: border-box;
  }
  