.bubblingG {
	text-align: center;
	width:auto;
	height:auto;
	display: flex;
	justify-content: center;
}
.bubblingG span {
	width: 17px;
	height: 17px;
	background: #ff1212;
	border-radius: 86px;
	animation: bubblingG 0.6s infinite alternate;
}
.bubblingG > div:nth-child(even) {
	margin: 0 2px;
}
#bubblingG_1 {
	animation-delay: 0s;
}
#bubblingG_2 {
	animation-delay: 0.18s;
}
#bubblingG_3 {
	animation-delay: 0.36s;
}
@keyframes bubblingG {
	0% {
		width: 17px;
		height: 17px;
		background-color:#ff1212;
		transform: translateY(0);
}
	100% {
		width: 41px;
		height: 41px;
		background-color:#fcff57;
		transform: translateY(-36px);
	}
}
