
#chat-container{
    background-color: lightblue;
    width: 100%;
    height: 640px;
    overflow: scroll;
  }
  .border{
    width: 75%;
  }
  .chat-section{
    display: block;
  } 
  .offline-status{
    color: red;
    width: 5px;
    font-size: small;
  }
  .online-status{
    color: greenyellow;
    width: 5px;
    font-size: small;
  }
  .current-user-chat{
    text-align: right;
    margin: 10px;
    color: black;
  }
  .distance-user-chat{
    text-align: left;
    margin: 10px;
    color: black;
  }
  .fa-trash{
    color: red;
  }